import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

/* let inputField = document.querySelector(".inputField"); */
let inputTodo = document.querySelector(".inputTodo");
let todoList = document.querySelector(".todoList");
let addNew = document.querySelector("addNew");

let createNewTodo = () => {
  let newTodoId = todos.length + 1;
  let newInputTodo = inputTodo.value;

  let newTodo = {
    todoID: newTodoId,
    todoText: newInputTodo,
    todoComplete: false
  };
  todos = [todos, ...newTodo];
};

todos.forEach((todo) => {
  let todoContent = document.createElement("li");
  todoContent.textContent = todo.todoText;
  todoList.appendChild(todoContent);
});

addNew.addEventListener("click", createNewTodo);
